<template>
    <div id="container">
      <canvas width="320" height="240" id="picture"></canvas>
      <input id="Take-Picture" type="file" accept="image/*;capture=camera" />
      <p id="textbit">result: {{decoded}}</p>
      <LoadingBox v-if="loading"></LoadingBox>
    </div>

</template>

<script>
import axios from 'axios';
import LoadingBox from './LoadingBox'

export default {
  name: 'WebCodeCamJs',
  components: {
    LoadingBox,
  },
  props: {
    user: String,
  },
  mounted () {
      var takePicture = document.querySelector("#Take-Picture"),
       showPicture = document.createElement("img");
      var Result = this;
      var canvas = document.getElementById("picture");
      console.log(canvas);
      var ctx = canvas.getContext("2d");
      JOB.Init();
      JOB.SetImageCallback(function (result) {
        if (result.length > 0) {
          var tempArray = [];
          for (var i = 0; i < result.length; i++) {
            tempArray.push(result[i].Format + " : " + result[i].Value);
            if (result[i].Value[0] === 0) {
              result[i].Value = result[i].Value.slice();
            }
            Result.code = result[i].Value;
          }
          Result.decoded = tempArray.join("<br />");
          Result.upload();
        } else {
          if (result.length === 0) {
            Result.decoded = "Decoding failed.";
          }
        }
      });
      JOB.PostOrientation = true;
      JOB.OrientationCallback = function (result) {
        canvas.width = result.width;
        canvas.height = result.height;
        var data = ctx.getImageData(0, 0, canvas.width, canvas.height);
        for (var i = 0; i < data.data.length; i++) {
          data.data[i] = result.data[i];
        }
        ctx.putImageData(data, 0, 0);
      };
      JOB.SwitchLocalizationFeedback(true);
      JOB.SetLocalizationCallback(function (result) {
        ctx.beginPath();
        ctx.lineWIdth = "2";
        ctx.strokeStyle = "red";
        for (var i = 0; i < result.length; i++) {
          ctx.rect(result[i].x, result[i].y, result[i].width, result[i].height);
        }
        ctx.stroke();
      });
      if (takePicture && showPicture) {
        takePicture.onchange = function (event) {
          var files = event.target.files;
          if (files && files.length > 0) {
            var file = files[0];
            try {
              var URL = window.URL || window.webkitURL;
              showPicture.onload = function (event) {
                Result.decoded = '';
                JOB.DecodeImage(showPicture);
                URL.revokeObjectURL(showPicture.src);
              };
              showPicture.src = URL.createObjectURL(file);
            }
            catch (e) {
              try {
                var fileReader = new FileReader();
                fileReader.onload = function (event) {
                  showPicture.onload = function (event) {
                    Result.decoded = '';
                    JOB.DecodeImage(showPicture);
                  };
                  showPicture.src = event.target.result;
                };
                fileReader.readAsDataURL(file);
              }
              catch (e) {
                Result.decoded = "Neither createObjectURL or FileReader are supported";
              }
            }
          }
        };
      }
  },
  data () {
    return {
        decoded: 'none',
        result: {},
        code: '',
        loading: false,
    }
  },
  methods: {
    upload() {
      this.loading = true;
      axios
        .get(`https://beer.rhenium.jp/users/${this.user}/drink/${this.code}`)
        .then(response => {
          this.result = response.data;
          this.loading = false;
        })
    }
  },
}
</script>

<style>
canvas {
    border: 1px solid;
}
</style>
