<template>
  <div>
    今月飲んだアルコール量: {{amount}}ml
    <div class="case">
<div id="canvas-container">
    <canvas id="sineCanvas" width="800" height="300"></canvas>
</div>
      <div
        class="beer"
        :style="style"
      >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'pool-view',
  props: {
    amount: Number,
  },
  data() {
    return {
      drawData: {
        seconds: 0,
        t: 0,
      },
      canvas: '',
      context: '',
      canvas2: '',
      context2: '',
      height: '',
      width: '',
      xAxis: '',
      yAxis: '',
      unit: 100,
    }
  },
  mounted() {
    this.init();
  },
  computed: {
    style() {
      return {
        'height': `${this.amount / 2000}%`,
        'width': '100%',
        'background': 'linear-gradient(180deg, #F6F513, #ECE61A)',
      }
    },
  },
  methods: {
/**
 * Init function.
 *
 * Initialize variables and begin the animation.
 */
init() {

    this.canvas = document.getElementById("sineCanvas");

    this.canvas.width = document.documentElement.clientWidth; //Canvasのwidthをウィンドウの幅に合わせる
    this.canvas.height = document.documentElement.clientHeight-80;

    this.context = this.canvas.getContext("2d");

    this.height = this.canvas.height;
    this.width = this.canvas.width;

    this.xAxis = Math.floor(this.height * (2000 - this.amount) / 2000);
    this.yAxis = 0;

    this.draw();
},

/**
 * Draw animation function.
 *
 * This function draws one frame of the animation, waits 20ms, and then calls
 * itself again.
 */
draw() {

    // キャンバスの描画をクリア
    this.context.clearRect(0, 0, this.width, this.height);

    //波を描画
    this.drawWave('#10c2cd', 0.3, 3, 0);
    this.drawWave('#43c0e4', 0.4, 2, 250);
    this.drawWave('#1d82b6', 0.2, 1.6, 100);

    // Update the time and draw again
    this.drawData.seconds = this.drawData.seconds + .014;
    this.drawData.t = this.drawData.seconds*Math.PI;
    setTimeout(this.draw, 35);
},


/**
* 波を描画
* drawWave(色, 不透明度, 波の幅のzoom, 波の開始位置の遅れ)
*/
drawWave(color, alpha, zoom, delay) {
    this.context.fillStyle = color;
    this.context.globalAlpha = alpha;

    this.context.beginPath(); //パスの開始
    this.drawSine(this.drawData.t / 0.5, zoom, delay);
    this.context.lineTo(this.width + 10, this.height); //パスをCanvasの右下へ
    this.context.lineTo(0, this.height); //パスをCanvasの左下へ
    this.context.closePath() //パスを閉じる
    this.context.fill(); //塗りつぶす
},

/**
 * Function to draw sine
 *
 * The sine curve is drawn in 10px segments starting at the origin.
 * drawSine(時間, 波の幅のzoom, 波の開始位置の遅れ)
 */
drawSine(t, zoom, delay) {

    // Set the initial x and y, starting at 0,0 and translating to the origin on
    // the canvas.
    var x = t; //時間を横の位置とする
    var y = Math.sin(x)/zoom;
    this.context.moveTo(this.yAxis, this.unit*y+this.xAxis); //スタート位置にパスを置く

    // Loop to draw segments (横幅の分、波を描画)
    for (let i = this.yAxis; i <= this.width + 10; i += 10) {
        x = t+(-this.yAxis+i)/this.unit/zoom;
        y = Math.sin(x - delay)/10;
        this.context.lineTo(i, this.unit*y+this.xAxis);
    }
},
  },
}
</script>

<style lang="scss">
.case {
  position: absolute;
  top: 0;
  bottom: 80px;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
#canvas-container {
  margin-bottom: -20px;
  border: none;
}
#sineCanvas {
  border:none;
}
</style>
