<template>
  <div>
    <PoolView v-bind:amount="amount" />
  </div>
</template>

<script>
import PoolView from '../components/PoolView'
import axios from 'axios'

export default {
  name: 'pool',
  props: {
    user: String,
  },
  components: {
    PoolView,
  },
  data() {
    return {
      amount: 0,
    }
  },
  mounted() {
    var date = new Date();
    date.setDate(1);
    axios
      .get(`https://beer.rhenium.jp/users/${this.user}/?since=${date.getTime()}`)
      .then(response => {
        this.amount = response.data.sumAlc;
      })
  },
}
</script>
