<template>
  <div>
    history page.
    <ul>
      <li v-for='(value, index) in histories' :key='index'>
        <HistoryCard v-bind:history="value"></HistoryCard>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'
import HistoryCard from '../components/HistoryCard'

export default {
  name: 'history',
  props: {
    user: String,
  },
  components: {
    HistoryCard,
  },
  data() {
    return {
      histories: {},
    }
  },
  mounted() {
    axios
      .get(`https://beer.rhenium.jp/users/${this.user}/`)
      .then(response => {
        this.histories = response.data.history;
      })
  },
}
</script>

<style lang="scss" scoped>
ul {
  list-style: none;
  padding: 0;
  margin-bottom: 100px;
}
</style>
