import Vue from 'vue'
import Router from 'vue-router'
//import Home from './views/Home'
import Scan from './views/Scan'
import Pool from './views/Pool'
import History from './views/History'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Pool
    },
    {
      path: '/scan',
      name: 'scan',
      component: Scan
    },
    {
      path: '/history',
      name: 'history',
      component: History,
    },
  ]
})
