<template>
  <div class="card">
    <div class="name">{{history.name}}</div>
    <div class="row">
      <div class="amount">{{history.ml}}ml</div>
      <div class="alc">{{history.alc}}%</div>
    </div>
    <div class="date">{{date}}</div>
  </div>
</template>

<script>
export default {
  name: 'history-card',
  props: {
    history: Object,
  },
  data() {
    return {
      date: new Date(),
    }
  },
  mounted() {
    this.date = new Date(this.history.time).toLocaleString('ja-JP');
  },
}
</script>

